#app {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: center;
	align-content: center; }

h1 {
	text-align: center;
	text-transform: uppercase; }

.tab {
	border: 2px solid #ababab;
	border-radius: 10px;
	padding: 5px 20px;
	text-align: center;
	box-shadow: 3px 3px 6px #ababab;
	min-width: 200px;
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 30px;
	h3 {
		flex-basis: 100%; } }

.square {
	width: 30px;
	height: 30px;
	border: 1px solid black; }

.square:hover {
	cursor: pointer; }

.kit {
	position: absolute;
	left: 0;
	border: 1px solid black;
	width: 50px;
	height: 100%;
	display: flex;
	flex-direction: column;
	&__square {
		border-bottom: 1px dashed #000;
		height: 50px;
		box-sizing: border-box;
		> div {
			width: 100%;
			height: 100%;
			box-sizing: border-box; } }

	&__color--yellow {
		background-color: yellow; }
	&__color--green {
		background-color: green; } }

.active {
	border: 2px solid blue; }
